import React from 'react';
import PopupWithForm from '../PopupWithForm/PopupWithForm';
import { useLocation } from 'react-router-dom';

function PopupDeleteCardConfirm({
  isOpen,
  onClose,
  confirmEventDelete,
  confirmAudienceDelete,
}) {

  const location = useLocation();

  function handleSubmitEvent(event) {
    console.log(' c ');
    event.preventDefault();
    confirmEventDelete();
  };

  function handleSubmitAudience(event) {
    console.log(' c  ');
    event.preventDefault();
    confirmAudienceDelete();
  };

  return (
    <>
      {location.pathname === '/sends' && (
        <PopupWithForm
          name='delete-card'
          title=' ?'
          subtitle='  '
          id='form-delete-card'
          formName='delete-place-card'
          buttonTextConfirm=''
          buttonText=''
          onSubmit={handleSubmitEvent}
          isOpen={isOpen}
          onClose={onClose}
        />
      )}

      {location.pathname === '/audience' && (
        <PopupWithForm
          name='delete-card'
          title=' ?'
          subtitle='  '
          id='form-delete-card'
          formName='delete-place-card'
          buttonTextConfirm=''
          buttonText=''
          onSubmit={handleSubmitAudience}
          isOpen={isOpen}
          onClose={onClose}
        />
      )}
    </>
  );
}

export default PopupDeleteCardConfirm;