export const COLUMNS = [
    {
        Header: 'ID',
        accessor: 'id',
    },
    {
        Header: ' ',
        accessor: 'date',
    },
    {
        Header: ' ',
        accessor: 'date_of_update',
    },
    {
        Header: '',
        accessor: 'name',
    },
    {
        Header: '',
        accessor: 'value',
    },
    {
        Header: '',
        accessor: 'deep',
    },
    {
        Header: '',
        accessor: ({categorys}) => {
            if (categorys !== undefined) {
                return categorys.reduce((accumulator, currentValue) => accumulator+currentValue.category+' ' ,'')
            } else {
                return ''
            }
        },
    }
]

